import Slider from "./slider";

export default class MainSlider extends Slider {
    constructor(obj) {
        super(obj); 
    }

    showSlides(n) {
        if (n > this.slides.length) {
            this.slideIndex = 1;
        } 

        if (n < 1) {
            this.slideIndex = this.slides.length;
        }

        try {
            this.hanson.style.opacity = '0';        
            if (n === 3){
                this.hanson.classList.add('animated');
                setTimeout(() => {
                    this.hanson.style.opacity = '1';
                    this.hanson.classList.add('slideInUp');           
                }, 3000);
            } else {
                this.hanson.classList.remove('slideInUp');           
            }
        } catch(error) {}

        Array.from(this.slides).forEach(slide => {
            slide.style.display = 'none'
        });

        Array.from(this.slides)[this.slideIndex-1].style.display = 'block';
    }

    plusSlides(n) {
        this.showSlides(this.slideIndex += n)
    }

    bindTriggers() {

        this.modulePrevBtn.forEach(item => {
            item.addEventListener('click', (e) => {
                e.stopPropagation();
                e.preventDefault();
                this.plusSlides(-1);
            })
        })

        this.moduleNextBtn.forEach(item => {
            item.addEventListener('click', (e) => {
                e.stopPropagation();
                e.preventDefault();
                this.plusSlides(1);
            })
        })
    }

    render() {
         if (this.container) {
            this.btns.forEach(btn => {
                btn.addEventListener('click', () => this.plusSlides(1));
                try {
                    this.hanson = document.querySelector('.hanson');
    
                } catch(e){}
            btn.parentElement.previousElementSibling.addEventListener('click', (e) => {
                e.preventDefault();
                this.slideIndex = 1;
                this.showSlides(this.slideIndex);
            });
                });

            this.showSlides(this.slideIndex);  
            this.bindTriggers();   
        }
    }
}
